<template>
    <div class="add-address-box">
        <div class="tips">
            <i class="iconfont icon-tanhao"></i> </div>
        <div class="ui-input-list">
            <li>
                <div class="m"></div>
                <div class="f"><input type="text" maxlength="4" v-model="save_data.user_name" class="ui-input needsclick" placeholder=""></div>
            </li>

            <li>
                <div class="m"></div>
                <div class="f"><input type="tel" maxlength="11" class="ui-input needsclick" v-model="save_data.phone" placeholder=""></div>
            </li>

            <li>
                <div class="m"></div>
                <div class="f">
                    <div>
                        <ui-area v-model="area"></ui-area>

                    </div>
                    <div><input type="text" class="ui-input needsclick" v-model="save_data.address" placeholder=""></div>
                </div>
            </li>

        </div>
        <div class="space"></div>
        <div class="btn-wrap">
            <div class="btn-fixed">
                <button type="button" class="ui-btn ui-btn-block ui-btn-l2" @click="save()"></button>
            </div>
        </div>
    </div>
</template>
<script>
import http from '@/assets/js/http'
import uiArea from './area'
export default {
    name: 'class',
    mixins: [http],
    components: {
        uiArea
    },
    data() {
        return {
            area: {                
                province:'',
                city:'',
                area:''
            },
            save_data: {

            },
            isEdit: 0
        }
    },
    methods: {
        //
        save(){
            let url = this.isEdit == 1 ? '/mini/Address/edit' : '/mini/Address/add', vm = this, data = this.save_data;
            data.province = this.area.province;
            data.city = this.area.city;
            data.area = this.area.area;
            this.apiPost(url, data).then(function(res) {
                if (res.code) {
                    // vm.$msg(res.msg);
                    // vm.goto('/address');
                    history.go(-1);
                } else {
                    vm.handleError(res)
                }
            })
        },
        //
        get_info(id) {
            let url = '/mini/Address/detail?id=' + id,
                vm = this;
            this.apiGet(url).then(function(res) {
                if (res.code) {
                    vm.save_data = res.data;
                  
                    vm.area = {
                        province : res.data.province,
                        city : res.data.city,
                        area : res.data.area,
                    }
                } else {
                }
            })
        }
    },
    created() {
        this.setTitle('')

        if (this.$route.name == 'editAddress') {
            this.setTitle('')
            this.isEdit = true;
            this.get_info(this.$route.params.id);
        }
    }


}

</script>



